@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,wght@600,bold,italic&subset=latin,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:#fff;color:#000;font-family:'Source Sans Pro', 'Calibri', 'Cambria', sans-serif}.App{min-width:350px;margin-bottom:1em}.Resume{margin:0 2.9em 0}main{display:flex;flex-flow:row wrap;margin-right:-2em}.experience{flex:8 1;min-width:56%;margin-right:2em}.sidebar{flex:4 1;min-width:350px;margin-right:2em}h1{color:#03506f;font-size:3rem;font-weight:normal;-webkit-margin-before:0.75em;margin-block-start:0.75em;-webkit-margin-after:0.1em;margin-block-end:0.1em}h2{color:#03506f;font-size:1.4rem;font-weight:normal;border-bottom:1px solid #000;line-height:1.4em;-webkit-margin-after:0.4em;margin-block-end:0.4em}h3{font-size:1.05rem;line-height:1.1em;-webkit-margin-before:0;margin-block-start:0;-webkit-margin-after:0;margin-block-end:0}h4{font-size:1.05rem;font-weight:normal;font-style:italic;-webkit-margin-before:0;margin-block-start:0;-webkit-margin-after:0.1em;margin-block-end:0.1em}header{display:flex;justify-content:space-between;align-items:center}header .name{flex:6 1}header .links{flex:5 1;text-align:right;margin-top:2.4em}header .email{margin-bottom:2px}.experience ul.experienceList,.education ul.educationList{-webkit-margin-before:0;margin-block-start:0;-webkit-padding-start:0;padding-inline-start:0}.experience ul.experienceList{-webkit-margin-after:0;margin-block-end:0}.experience li.experienceItem,.education li.educationItem{list-style:none;margin-bottom:1em}.experience li.experienceItem:last-child,.education li.educationItem:last-child{margin-bottom:0}.experience li.experienceItem .summary,.education li.educationItem .summary{display:flex}.experience li.experienceItem .info,.education li.educationItem .info{flex:1 1;margin-right:16px}.experience li.experienceItem .context .education li.educationItem .context{flex:1 1}.experience li.experienceItem .when,.education li.educationItem .when{font-size:1.05rem;line-height:1.1em;font-weight:bold;text-align:right}.experience li.experienceItem .where{font-style:italic;text-align:right}.experience ul.details{list-style:none}.experience .details li{margin-left:-1em}.experience .details li:before{content:"•";color:#03506f;display:inline-block;width:1em;margin-left:-1em}.skillsList .category{margin-bottom:0.7em}.educationList .title{font-weight:bold;line-height:1em}.educationList .gpa{text-align:right}.educationList .coursework{-webkit-margin-before:0.5em;margin-block-start:0.5em}@media print{html{font-size:11.2px}.App{margin:-2em 0 0}.Resume{flex-basis:100%}main{display:block}.experience{flex:8 1}main,.experience,.sidebar{margin:0}a{color:black;text-decoration:none}}@media only screen and (max-width: 450px), only screen and (max-height: 450px){.Resume{margin:0 5% 0}main{flex-direction:column}main,.experience,.sidebar{margin-right:0;min-width:0}h3,.experience li.experienceItem .when,.education li.educationItem .when,.educationList .title{font-weight:bold}.experience .details li{margin-left:-1em}}@media only screen and (max-width: 450px){header{flex-direction:column}header .links{text-align:center;margin-top:0}}

